import { fetchImages } from './js/pixabay-api.js';
import { renderImages, showEndMessage, hideLoadMoreButton } from './js/render-functions.js';
import iziToast from 'izitoast';
import 'izitoast/dist/css/iziToast.min.css';
import './css/styles.css';

const form = document.querySelector('.search-form');
const loadMoreButton = document.querySelector('.load-more');
const loader = document.getElementById('loading-overlay'); // –û–≤–µ—Ä–ª–µ–π –ª–æ–∞–¥–µ—Ä–∞

let searchQuery = '';
let currentPage = 1;
const perPage = 40;
const MAX_PAGES = 5;
let totalHits = 0;
let loadedImages = new Set();

// ‚úÖ –§—É–Ω–∫—Ü–∏—è –æ—Ç–æ–±—Ä–∞–∂–µ–Ω–∏—è –ª–æ–∞–¥–µ—Ä–∞
function showLoader() {
  if (loader) {
    loader.style.display = 'flex';
    loader.style.opacity = '1'; // –ü–ª–∞–≤–Ω–æ–µ –ø–æ—è–≤–ª–µ–Ω–∏–µ
  }
}

// ‚úÖ –§—É–Ω–∫—Ü–∏—è —Å–∫—Ä—ã—Ç–∏—è –ª–æ–∞–¥–µ—Ä–∞
function hideLoader() {
  if (loader) {
    setTimeout(() => {
      loader.style.opacity = '0'; // –ü–ª–∞–≤–Ω–æ–µ –∏—Å—á–µ–∑–Ω–æ–≤–µ–Ω–∏–µ
      setTimeout(() => {
        loader.style.display = 'none';
      }, 300); // –î–∞–µ–º –≤—Ä–µ–º—è –¥–ª—è –∞–Ω–∏–º–∞—Ü–∏–∏
    }, 500); // –û—Å—Ç–∞–≤–ª—è–µ–º –µ–≥–æ –≤–∏–¥–∏–º—ã–º –Ω–∞ 0.5—Å –ø–µ—Ä–µ–¥ —Å–∫—Ä—ã—Ç–∏–µ–º
  }
}

// ‚úÖ –§—É–Ω–∫—Ü–∏—è –ø–ª–∞–≤–Ω–æ–π –ø—Ä–æ–∫—Ä—É—Ç–∫–∏ –ø–æ—Å–ª–µ –∑–∞–≥—Ä—É–∑–∫–∏ –Ω–æ–≤—ã—Ö –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–π
function smoothScroll() {
  const firstGalleryItem = document.querySelector('.gallery-item');
  if (firstGalleryItem) {
    const cardHeight = firstGalleryItem.getBoundingClientRect().height;
    window.scrollBy({ top: cardHeight * 2, behavior: 'smooth' });
  }
}

// ‚úÖ –§—É–Ω–∫—Ü–∏—è –∑–∞–≥—Ä—É–∑–∫–∏ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–π
async function loadImages(query, page, isLoadMore = false) {
  if (!query.trim()) {
    iziToast.warning({
      title: 'Warning',
      message: 'Please enter a valid search term!',
      position: 'topRight',
    });
    return;
  }

  showLoader();

  try {
    const response = await fetchImages(query, page, perPage);

    if (!response || response.totalHits === 0) {
      hideLoader();
      return;
    }

    totalHits = Math.min(response.totalHits, 500);
    const newImages = response.hits.filter(image => !loadedImages.has(image.id));

    if (newImages.length > 0) {
      renderImages(newImages, currentPage > 1);
      newImages.forEach(image => loadedImages.add(image.id));

      if (isLoadMore) {
        smoothScroll();
      }
    }

    if (currentPage >= MAX_PAGES || totalHits <= currentPage * perPage) {
      hideLoadMoreButton();
      showEndMessage();
    } else {
      loadMoreButton.style.display = 'block';
    }

  } catch (error) {
    iziToast.error({
      title: 'Error',
      message: 'Failed to load images. Please try again!',
      position: 'topRight',
    });
  } finally {
    hideLoader();
  }
}

// ‚úÖ –û–±—Ä–∞–±–æ—Ç—á–∏–∫ –æ—Ç–ø—Ä–∞–≤–∫–∏ —Ñ–æ—Ä–º—ã (–Ω–æ–≤—ã–π –ø–æ–∏—Å–∫)
form.addEventListener('submit', async event => {
  event.preventDefault();
  searchQuery = event.target.elements.searchQuery.value.trim();

  if (!searchQuery) {
    iziToast.warning({
      title: 'Warning',
      message: 'Please enter a valid search term!',
      position: 'topRight',
    });
    return;
  }

  currentPage = 1;
  totalHits = 0;
  loadedImages.clear();
  hideLoadMoreButton();
  showLoader();

  try {
    await loadImages(searchQuery, currentPage, false);
  } finally {
    hideLoader();
  }
});

// ‚úÖ –û–±—Ä–∞–±–æ—Ç—á–∏–∫ –∫–Ω–æ–ø–∫–∏ "Load More"
loadMoreButton.addEventListener('click', async () => {
  if (currentPage >= MAX_PAGES) {
    hideLoadMoreButton();
    showEndMessage();
    return;
  }

  currentPage += 1;
  showLoader();

  try {
    await loadImages(searchQuery, currentPage, true);
  } finally {
    hideLoader();
  }
});

// ‚úÖ –õ–æ–∞–¥–µ—Ä –ø—Ä–∏ –æ—Ç–∫—Ä—ã—Ç–∏–∏ SimpleLightbox
import SimpleLightbox from 'simplelightbox';
import 'simplelightbox/dist/simple-lightbox.min.css';

const lightbox = new SimpleLightbox('.gallery a', {
  captionsData: 'alt',
  captionDelay: 250,
  beforeShow: () => showLoader(),
  afterShow: () => hideLoader(),
});

// ‚úÖ –î–æ–±–∞–≤–ª—è–µ–º HTML-—Å—Ç—Ä—É–∫—Ç—É—Ä—É –ª–æ–∞–¥–µ—Ä–∞ –≤ `index.html`, –µ—Å–ª–∏ –æ–Ω –æ—Ç—Å—É—Ç—Å—Ç–≤—É–µ—Ç
if (!document.getElementById('loading-overlay')) {
  const loaderHTML = `
    <div id="loading-overlay">
      <span class="loader"></span>
    </div>
  `;
  document.body.insertAdjacentHTML('beforeend', loaderHTML);
}
































